}

.contacts-form {
  background-color: rgb(204, 140, 189);
  box-shadow: 0 0 8px rgba(0, 0, 0, 0, 01);
  /* padding: 30px; */
  font-family: Oswald;
  /* padding-left: 1rem; */
  /* display: grid; */
  padding-top: 35px;
  font-size: 1.5rem;
}

.contacts-label {
  font-family: open sans;
  /* text-align: left; */
  /* float: left; */
  /* display: block; */
  /* margin-bottom: 20px; */
}

.contacts-select {
  font-family: open sans;
  padding: 12px 20px;
  margin: 8px 0;
  margin-bottom: 20px;
  width: 100%;
  height: 40px;
  border-radius: 0.8vh;
}

.contacts-gender {
  margin-bottom: 20px;
}

.contacts-textarea {
  height: 60px;
  border-radius: 0.8vh;
  font-family: open sans;
}

.contacts-image {
  width: 600px;
  height: 100%;
  object-fit: cover;
  padding-left: 2rem;
}

@media screen and (max-width: 800px) {
  .contacts-image {
    display: none;
  }
}
